<template>
    <div class="playMusicPage">
        <div class="bg" :style="{backgroundImage: `url(${playDetail.al.picUrl})`}"></div>
        <div class="playTop">
            <div class="back" @click="$emit('back')">
                <i class="iconfont icon-zuojiantou"></i>
            </div>
            <div class="center">
                <div class="title">
                    <marquee behavior="scroll" direction="left">
                        {{playDetail.name}}
                    </marquee>
                </div>
            </div>
            <div class="share">
                <i class="iconfont icon-fenxiang"></i>
            </div>
        </div>
        <div class="playContent" v-show="!isLyric" @click="togglePage">
            <img class="disc" src="@/assets/img/1.png">
            <img :class="['needle', {active: !audio.paused}]" src="@/assets/img/2.png">
            <img class="playImg" :src="playDetail.al.picUrl">
        </div>
        <div class="playLyric" v-show="isLyric" @click="togglePage">
            <div class="lyricNav" ref="playLyric">
                <div
                    v-for="(item, i) in lyricList" :key="i"
                    :class="['ly',{active: (item.time <= currentTime*1000 && currentTime*1000 < item.nextTime)}]"
                >
                    <span v-if="item.content">
                        {{item.lyric}}
                    </span>
                    <span v-else>
                        {{item}}
                    </span>
                </div>
            </div>
        </div>
        <div class="progress">

        </div>
        <div class="playFooter">
            <i class="iconfont icon-xunhuan"></i>
            <i class="iconfont icon-shangyishoushangyige" @click="go(-1)"></i>
            <i :class="`iconfont icon-${playIcon}`" @click="playMusic(audio)"></i>
            <i class="iconfont icon-xiayigexiayishou" @click="go(1)"></i>
            <i class="iconfont icon-24gf-playlistHeart"></i>
        </div>
    </div>
</template>

<script>
import {  ref } from '@vue/reactivity'
import { mapGetters, mapMutations, mapState } from 'vuex'
import { watch } from '@vue/runtime-core'
import { useStore } from 'vuex'

export default {
    props: ['playDetail', 'audio'],
    emits: ['back'],
    computed:{
        ...mapState(['playIcon', 'lyric', 'currentTime']),
        ...mapGetters(['lyricList'])
    },
    methods:{
        ...mapMutations(['playMusic'])
    },
    setup(props){
        const store = useStore()
        let isLyric = ref(false)
        let active = ref('')
        // 设置歌词滚动
        let playLyric = ref('')
        watch(() => store.state.currentTime, () => {
            if(store.state.lyric.match(/(\d+)/g)){
                let div = document.querySelector('.ly.active')
                if(div){
                    playLyric.value.scrollTop = div.offsetTop  
                }
            }
        })

        // cd和歌词界面切换
        function togglePage(){
            isLyric.value = !isLyric.value
        }

        // 上一首、下一首
        function go(num){
            let index = store.state.playCurrentIndex+num
            if(index < 0){
                index = store.state.playlist.length-1
            }else if(index == store.state.playlist.length){
                index = 0
            }
            store.commit('setPlayIndex', index)
            setTimeout(() => {
                store.commit('playMusic', props.audio)
            },10)
        }        

        return {
            active,
            isLyric,
            playLyric,
            go,
            togglePage
        }
    }
}
</script>

<style lang="less" scoped>
.playMusicPage{
    position: fixed;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    background-color: #fff;
    .bg{
        position: absolute;
        left: 0;
        top: 0;
        width: 7.5rem;
        height: 100%;
        background-size: auto 100%;
        filter: blur(10px);
        background-position: center;
    }
    .playTop{
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 7.5rem;
        height: 1.2rem;
        padding: 0 0.2rem;
        position: absolute;
        z-index: 333;
        left: 0;
        top: 0;
        color: #fff;
        .iconfont{
            font-size: 0.6rem;
        }
        marquee{
            width: 5rem;
            padding: 0 0.2rem;
        }
    }
    .playContent{
        position: absolute;
        width: 7.5rem;
        height: 10rem;
        left: 0;
        top: 1.6rem;
        .needle{
            width: 2.2rem;
            position: absolute;
            left: 3.3rem;
            transform: rotate(-30deg);
            transform-origin: 0.3rem 0;
            transition: all 1s;
        }
        .needle.active{
            transform: rotate(3deg);
        }
        .disc{
            width: 5.5rem;
            height: 5.5rem;
            position: absolute;
            left: 0;
            top: 0;
            bottom: 0;
            right: 0;
            margin: auto;
        }
        .playImg{
            width: 3.45rem;
            height: 3.45rem;
            border-radius: 50%;
            position: absolute;
            left: 0;
            top: 0;
            bottom: 0;
            right: 0;
            margin: auto;
        }
    }
    .playFooter{
        width: 7.5rem;
        height: 1.7rem;
        padding: 0 0.2rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: absolute;
        left: 0;
        bottom: 0;
        color: #fff;
        .iconfont{
            font-size: 0.7rem;
        }
    }
    .playLyric{
        position: absolute;
        background: rgba(192, 192, 192, 0.4);
        width: 7.5rem;
        height: 100%;
        text-align: center;
        color: rgb(255, 255, 255);
        padding: 0 1rem;
        .lyricNav{
            width: 5.5rem;
            height: 9rem;
            overflow: scroll;
            margin-top: 1.5rem;
            position: relative;
            div{
                padding: 0.1rem 0;
            }
            .active{
                color: rgb(255, 0, 0);
            }
        }
    }
}
</style>