<template>
    <div class="playController">
        <div class="playControllerNav">
            <div class="left">
                <img :src="playlist[playCurrentIndex].al.picUrl" @click="toggleShow">
                <div class="content">
                    <div class="title">{{playlist[playCurrentIndex].name}}</div>
                    <div class="tips">横滑可以切换上下首哦~</div>
                </div>
            </div>
            <div class="right">
                <i :class="`iconfont icon-${playIcon}`" @click="playMusic(audio)"></i>
                <i class="iconfont icon-24gf-playlistHeart"></i>
            </div>
        </div>
        <MusicPlayPage
            @back="toggleShow"
            v-show="isShow"
            :playDetail="playlist[playCurrentIndex]"
            :audio="audio"
        />
        <audio
            ref="audio"
            :src="`https://music.163.com/song/media/outer/url?id=${playlist[playCurrentIndex].id}.mp3`"
        ></audio>
    </div>
</template>

<script>
import { mapActions, mapMutations, mapState } from 'vuex'
import { getCurrentInstance, onMounted, onUpdated, ref } from 'vue'
import store from '@/store'
import MusicPlayPage from '@/components/MusicPlayPage.vue'

export default {
    components:{
        MusicPlayPage
    },
    computed:{
        ...mapState(['playlist', 'playCurrentIndex', 'playIcon'])
    },
    methods:{
        ...mapMutations(['playMusic']),
        ...mapActions(['reqLyric'])
    },
    mounted(){
        this.reqLyric(this.playlist[this.playCurrentIndex])
    },
    updated(){
        this.reqLyric(this.playlist[this.playCurrentIndex])
    },
    setup(){
        // 切换歌曲时，使音乐自动播放
        let audio = ref('')
        const { proxy } = getCurrentInstance()
        proxy.$EventBus.on('playMusic',() => {
            setTimeout(() => {
                store.commit('playMusic', audio.value)
            },10)
        })

        // 将audio提取到store，方便操作
        onMounted(() => {
            store.commit('setAudio', audio.value)
        })

        onUpdated(() => {
            store.commit('setPlayIcon', audio.value)
        })

        // 控制音乐唱片界面显示
        let isShow = ref(false)
        function toggleShow(){
            isShow.value = !isShow.value
        }

        return {
            audio,
            isShow,
            toggleShow
        }
    }
}
</script>

<style lang="less" scoped>
.playController{
    width: 7.5rem;
    height: 1.2rem;
    position: fixed;
    left: 0;
    bottom: 0;
    background: #fff;
    border-top: 1px solid #ccc;
    display: flex;
    align-items: center;
    padding: 0 0.2rem;
    z-index: 2;
    .playControllerNav{
        width: 7.1rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .left{
            display: flex;
            img{
                width: 0.8rem;
                height: 0.8rem;
                border-radius: 50%;
            }
            .content{
                margin-left: 0.2rem;
                .tips{
                    font-size: 0.2rem;
                    color: #999;
                }
            }
        }
        .right{
            display: flex;
            width: 1.2rem;
            justify-content: space-between;
            .iconfont{
                font-size: 0.45rem;
            }
        }
    }
}
</style>