import { createStore } from 'vuex'
import { getLyric, phoneLogin, getUserDetail } from '@/api'

export default createStore({
    state: {
        playlist: [{
            al:{
                id: 127537487,
                name: "最好的都给你",
                pic: 109951165986850990,
                picUrl: "http://p4.music.126.net/PTv7Li70ydONxgrtfTcaNw==/109951165986850987.jpg",
                pic_str: "109951165986850987",
            },
            id: 1845651956,
            name: '最好的都给你',
        }],
        playCurrentIndex: 0,
        playIcon: 'bofang',
        lyric: '',
        audio: '',
        currentTime: 0,
        intervalId: '',
        user:{
            isLogin: false,
            account: {},
            userDetail: {}
        }
    },
    getters:{
        lyricList(state){
            let flag = state.lyric.match(/(\d+)/g)
            let arr
            if(flag){
                arr = state.lyric.trim().split(/\n/igs).map((item, i) => {
                    let min = item.slice(1,3)
                    let sec = item.slice(4,6)
                    let mill = item.slice(7,10).replace(/]/,'')
                    let lyric = item.slice(10).replaceAll(/]/g,'')
                    let time = parseInt(mill) + parseInt(sec)*1000 + parseInt(min)*60000
                    return {min, sec, mill, lyric, time, content:item}
                })
                arr.forEach((item, i, arr) => {
                    if(i == (arr.length-1)){
                        item.nextTime = 1000000
                    }else{
                        item.nextTime = arr[++i].time
                    }
                })
                return arr
            }
            arr = state.lyric.trim().split(/\n/igs)
            return arr
        }
    },
    mutations: {
        setPlaylist(state, value){
            state.playlist = value
        },
        setPlayIndex(state, value){
            state.playCurrentIndex = value
        },
        pushPlaylist(state,value){
            state.playlist.push(value)
        },
        playMusic(state, audio){
            if(audio.paused){
                audio.play()
                state.playIcon = 'zanting'
                clearInterval(state.intervalId)
                this.dispatch('updateTime')
            }else{
                audio.pause()
                state.playIcon = 'bofang'
                clearInterval(state.intervalId)
            }
        },
        setPlayIcon(state, audio){
            if(audio.paused){
                state.playIcon = 'bofang'
            }else{
                state.playIcon = 'zanting'
            }
        },
        // 设置歌词
        setLyric(state, value){
            state.lyric = value
        },
        // 获取音乐播放器
        setAudio(state, audio){
            state.audio = audio
        },
        // 设置音乐当前播放时间
        setCurrentTime(store, value){
            store.currentTime = value
        },
        setUser(state,value){
            state.user = value
        }
    },
    actions: {
        // 获取歌词
        async reqLyric(content, payload){
            let res = await getLyric(payload.id)
            // console.log(res)
            content.commit('setLyric', res.data.lrc.lyric)
        },

        // 更新音乐播放时间
        updateTime(content){
            content.state.intervalId = setInterval(() => {
                content.commit('setCurrentTime', content.state.audio.currentTime)
            },1000)
        },

        // 登录
        async phoneLogin(content, payload){
            let res = await phoneLogin(payload.phone, payload.password)
            if(res.status == 200){
                content.state.user.isLogin = true
                content.state.user.account = res.data.account
                let userDetail = await getUserDetail(res.data.account.id)
                content.state.user.userDetail = userDetail.data
                localStorage.userData = JSON.stringify(content.state.user)
            }
            return res
        }
    }
})
